                </div>
                <router-link class="checkmore" :to="{name: 'replyDetail'}">{{comment.commentNum}}>></router-link>

                <ul>
                   <li class="list">
                        <dl class="clearfix">
                            <dt><img src="../assets/images/ex1.png"></dt>
                            <dd>
                                <div class="row1">
                                    <span class="username"></span>
                                    <span class="time">2018-01-22  17:07</span>
