<script>
export default {
    name: 'QADetail',
        data() {
        return {
            radioGroup: [
                {title: '', value: 1},
                {title: '', value: 2},
            ],
            prodType: 1,
            isShowPhoto: true,
        }
    },
    methods:{
        clickRadio(){
            console.log(this.prodType);
        },
        submit(){
            this.$router.push({name: 'QADetail2'});
        }
    },
    computed:{
        radioActive(){
            console.log(this.prodType);
            if(this.prodType==1){
                this.isShowPhoto = true;
            }else{
                this.isShowPhoto = false;
            }
            return this.prodType||this.radioGroup[0].value;
        },
    }
}
</script>

<style lang="less" scoped>
@import '../assets/less/QADetail.less';
</style>



